:root {
  --bg: #f7f7f9;
  --fg: #121212;
  --muted: #666;
  --border: #e6e6ea;
  --card: #ffffff;

  --radius: 12px;
  --pad: 16px;

  /* Column widths */
  --pos-w: 28px;   /* you said 20 too small, 30 too big */
  --badge-w: 48px; /* cell width for image column */
  --row-h: 56px;   /* consistent row height */

  --pos-w: 28px;
  --badge-size: clamp(36px, 12vw, 5rem); /* responsive square */
  --row-h: clamp(56px, 12vw, 72px);
}

* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body { margin: 0; background: var(--bg); color: var(--fg); font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Arial, Helvetica, "Apple Color Emoji", "Segoe UI Emoji"; }

.page {
  max-width: 800px;
  margin: 0 auto;
  padding: 24px 16px 48px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 16px;
}

.title { font-size: 24px; font-weight: 700; margin: 0; }

.button {
  appearance: none;
  border: 1px solid var(--border);
  background: var(--card);
  padding: 8px 12px;
  border-radius: 10px;
  cursor: pointer;
}
.button:disabled { opacity: 0.6; cursor: default; }
.button:hover:enabled { box-shadow: 0 1px 8px rgba(0,0,0,0.06); }

.table {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  overflow: hidden;
  width: 100%;
}

/* Header row */
.row--head {
  background: #EFE6D7;
  font-size: clamp(12px, 3.2vw, 14px);
  color: var(--muted);
  font-weight: 600;
}

/* Rows */
.row {
  display: flex;
  align-items: center;
  min-height: var(--row-h);
  border-top: 1px solid var(--border);
  padding: 0 12px;
}
.row:first-child { border-top: 0; }
.row--head { border-top: 0; }

/* Cells */
.cell {
  display: flex;                 /* so we can center content easily */
  align-items: center;
  padding: 8px 8px;
}

/* Column sizing */
.cell--pos {
  width: var(--pos-w);
  justify-content: center;       /* center the rank horizontally */
  text-align: center;
  flex: 0 0 var(--pos-w);        /* fixed */
  min-width: 5vw;
}

.cell--team {
  flex: 1 1 auto;
  min-width: 0;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px; /* space between image and name */
}

.cell--badge {
  width: var(--badge-w);
  flex: 0 0 var(--badge-w);      /* fixed */
  justify-content: center;       /* center the image */
}

.cell--pts {
  justify-content: flex-end;
  flex: 0 0 clamp(90px, 22vw, 140px);
  font-variant-numeric: tabular-nums;
}

/* Nice borders between rows */
.table .row:not(.row--head) + .row { border-top: 1px solid var(--border); }


.badge {
  width: var(--badge-size);
  height: var(--badge-size);
  border-radius: 8px;
  object-fit: cover;
  flex: 0 0 var(--badge-size);
}

/* Errors */
.errors {
  margin-top: 16px;
  background: #fff6f6;
  border: 1px solid #ffd2d2;
  color: #9a1f1f;
  border-radius: var(--radius);
  padding: var(--pad);
}
.errors__title { font-weight: 700; margin-bottom: 6px; }

/* Footer */
.footer {
  margin-top: 16px;
  color: var(--muted);
  font-size: 12px;
}

/* ------- Responsive tweaks ------- */
@media (max-width: 640px) {
  /* Hide Pos if space is tight */
  .cell--pos { display: none; }

  /* Let points column auto-size and stick to the right */
  .cell--pts {
    flex: 0 0 auto;
    width: auto;
    margin-left: auto;
  }

  /* Reduce row padding a touch */
  .row { padding: 0 10px; }
}

@media (max-width: 420px) {
  /* Wrap row so long team names don't crush points */
  .row {
    flex-wrap: wrap;
    row-gap: 6px;
  }
}

.team-name {
  font-weight: 700;
  font-size: clamp(16px, 4.2vw, 20px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.team-players {
  font-weight: 400;
  color: #666;
  font-size: clamp(12px, 3.5vw, 14px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;}

.pos-number, .points-number {
  font-family: "Bungee Shade", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: clamp(18px, 6vw, 32px);

}

.team-container {
  display: flex;
  flex-direction: column;
  line-height: 1.2;
  padding-left: 0;                 /* we already have gap */
  min-width: 0;                    /* enable text truncation */

}